/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useState, useRef, ChangeEvent } from "react";
import Image from "next/image";
import { ProfileData, profileSchema } from "../schema";

interface ProfileFormProps {
  userId: string;
  initialData?: {
    name?: string;
    email?: string;
    bio?: string;
    phone?: string;
    image?: string;
  };
}

export default function ProfileForm({ userId, initialData }: ProfileFormProps) {
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");

  // Helper function to get proxied image URL
  const getProxiedImageUrl = (url: string | undefined) => {
    if (!url) {
      return `https://ui-avatars.com/api/?name=${encodeURIComponent(
        initialData?.name || "User"
      )}&size=200&background=fbb6ce&color=fff`;
    }

    // If it's a data URL (preview), return as-is
    if (url.startsWith('data:')) {
      return url;
    }

    // If it's already a full URL with localhost, proxy it
    if (url.includes('localhost') || url.includes('127.0.0.1')) {
      return `/api/image-proxy?url=${encodeURIComponent(url)}`;
    }

    // If it's a relative path, make it absolute and proxy it
    if (url.startsWith('/uploads')) {
      const fullUrl = `${process.env.NEXT_PUBLIC_API_BASE_URL}${url}`;
      return `/api/image-proxy?url=${encodeURIComponent(fullUrl)}`;
    }

    return url;
  };

  const defaultAvatar = getProxiedImageUrl(initialData?.image);

  const [imagePreview, setImagePreview] = useState<string>(defaultAvatar);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<ProfileData>({
    resolver: zodResolver(profileSchema),
    defaultValues: {
      name: initialData?.name || "",
      email: initialData?.email || "",
      bio: initialData?.bio || "",
      phone: initialData?.phone || "",
    },
  });

  const handleImageChange = (e: ChangeEvent<HTMLInputElement>) => {
    setError("");
    const file = e.target.files?.[0];
    if (!file) return;

    const validTypes = [
      "image/jpeg",
      "image/jpg",
      "image/png",
      "image/gif",
      "image/webp",
    ];

    if (!validTypes.includes(file.type)) {
      setError("Please upload a valid image (JPG, PNG, GIF, WebP)");
      return;
    }

    if (file.size > 5 * 1024 * 1024) {
      setError("Image size must be under 5MB");
      return;
    }

    const reader = new FileReader();
    reader.onloadend = () => {
      setImagePreview(reader.result as string);
    };
    reader.readAsDataURL(file);
    setSelectedFile(file);
  };

  const handleRemoveImage = () => {
    setImagePreview(defaultAvatar);
    setSelectedFile(null);
    if (fileInputRef.current) fileInputRef.current.value = "";
  };

  const onSubmit = async (data: ProfileData) => {
    setError("");
    setSuccess("");

    try {
      const formData = new FormData();
      
      formData.append("userId", userId);
      formData.append("name", data.name);
      formData.append("email", data.email);
      
      if (data.bio) {
        formData.append("bio", data.bio);
      }
      if (data.phone) {
        formData.append("phone", data.phone);
      }
      
      if (selectedFile) {
        formData.append("image", selectedFile);
      }

      const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/profile/update`, {
        method: "POST",
        body: formData,
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.message || "Failed to update profile");
      }

      setSuccess("Profile updated successfully!");
      
      // Use proxy for the returned image URL
      if (result.imageUrl) {
        const backendUrl = result.imageUrl.startsWith('http') 
          ? result.imageUrl 
          : `${process.env.NEXT_PUBLIC_API_BASE_URL}${result.imageUrl}`;
        
        // Proxy the URL
        const proxiedUrl = `/api/image-proxy?url=${encodeURIComponent(backendUrl)}`;
        
        setImagePreview(proxiedUrl);
        setSelectedFile(null);
        if (fileInputRef.current) {
          fileInputRef.current.value = "";
        }
      }
      
    } catch (err: any) {
      setError(err.message || "Failed to update profile. Please try again.");
      console.error("Profile update error:", err);
    }
  };

  return (
    <form
      onSubmit={handleSubmit(onSubmit)}
      className="space-y-6 p-6 bg-white rounded-lg shadow-md w-full max-w-md mx-auto"
    >
      {/* Messages */}
      {error && <p className="text-sm text-red-600">{error}</p>}
      {success && <p className="text-sm text-green-600">{success}</p>}

      {/* Profile Image */}
      <div className="flex flex-col items-center space-y-3">
        <div className="relative group">
          <div className="relative w-24 h-24 rounded-full overflow-hidden border-4 border-pink-200 shadow-lg">
            <Image
              src={imagePreview}
              alt="Profile Image"
              fill
              className="object-cover"
              priority
              unoptimized
            />
          </div>

          <div
            onClick={() => fileInputRef.current?.click()}
            className="absolute inset-0 rounded-full bg-black/50 opacity-0 group-hover:opacity-100 transition flex items-center justify-center cursor-pointer"
          >
            <span className="text-xs text-white font-medium">Change</span>
          </div>
        </div>

        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={handleImageChange}
          className="hidden"
        />

        <div className="flex gap-2 w-full">
          <button
            type="button"
            onClick={() => fileInputRef.current?.click()}
            className="flex-1 px-3 py-2 text-xs rounded-md bg-pink-100 text-pink-600 font-medium hover:bg-pink-200"
          >
            Choose Photo
          </button>

          <button
            type="button"
            onClick={handleRemoveImage}
            disabled={!selectedFile}
            className="flex-1 px-3 py-2 text-xs rounded-md bg-gray-100 text-gray-600 font-medium hover:bg-gray-200 disabled:opacity-50"
          >
            Remove
          </button>
        </div>

        <p className="text-xs text-gray-500 text-center">
          JPG, PNG, GIF, WebP â€¢ Max 5MB
        </p>
      </div>

      {/* Name */}
      <div className="space-y-1">
        <label htmlFor="name" className="text-sm font-medium">
          Full Name
        </label>
        <input
          id="name"
          type="text"
          placeholder="John Doe"
          className="h-10 w-full rounded-md border border-gray-300 bg-gray-50 px-3 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("name")}
        />
        {errors.name && (
          <p className="text-xs text-red-600">{errors.name.message}</p>
        )}
      </div>

      {/* Email */}
      <div className="space-y-1">
        <label htmlFor="email" className="text-sm font-medium">
          Email
        </label>
        <input
          id="email"
          type="email"
          placeholder="john@example.com"
          className="h-10 w-full rounded-md border border-gray-300 bg-gray-50 px-3 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("email")}
        />
        {errors.email && (
          <p className="text-xs text-red-600">{errors.email.message}</p>
        )}
      </div>

      {/* Phone */}
      <div className="space-y-1">
        <label htmlFor="phone" className="text-sm font-medium">
          Phone (optional)
        </label>
        <input
          id="phone"
          type="text"
          placeholder="9812345678"
          className="h-10 w-full rounded-md border border-gray-300 bg-gray-50 px-3 text-sm outline-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("phone")}
        />
        {errors.phone && (
          <p className="text-xs text-red-600">{errors.phone.message}</p>
        )}
      </div>

      {/* Bio */}
      <div className="space-y-1">
        <label htmlFor="bio" className="text-sm font-medium">
          Bio (optional)
        </label>
        <textarea
          id="bio"
          rows={3}
          placeholder="Tell us about yourself..."
          className="w-full rounded-md border border-gray-300 bg-gray-50 px-3 py-2 text-sm outline-none resize-none focus:border-pink-400 focus:ring-1 focus:ring-pink-400"
          {...register("bio")}
        />
        {errors.bio && (
          <p className="text-xs text-red-600">{errors.bio.message}</p>
        )}
      </div>

      {/* Submit Button */}
      <button
        type="submit"
        disabled={isSubmitting}
        className="h-10 w-full rounded-md bg-pink-400 text-white text-sm font-semibold hover:bg-pink-500 disabled:opacity-60"
      >
        {isSubmitting ? "Updating..." : "Update Profile"}
      </button>
    </form>
  );
}
